span[class^="rf-fi-"]::before, span[class*=" rf-fi-"]::before {
  font-family: icons !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  line-height: 1; }

@font-face {
  font-family: "icons";
  src: url("data:font/truetype;charset=utf-8;base64,") format("truetype"); }

span[class^="rf-fi-"]:not([class*="rf-fi--sm"]):not([class*="rf-fi--lg"]):not([class*="rf-fi--xl"])::before, span[class*=" rf-fi-"]:not([class*="rf-fi--sm"]):not([class*="rf-fi--lg"]):not([class*="rf-fi--xl"])::before {
  font-size: 1rem; }

/*
* @var: $icons-mames - générée dans generated/_icons.scss
*/
.rf-fi--sm::before {
  font-size: 0.75rem; }

.rf-fi--lg::before {
  font-size: 1.5rem; }

.rf-fi--xl::before {
  font-size: 2rem; }
